*
{
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

/* body
{
    background-color: #0B0C10;
} */

body::-webkit-scrollbar
{
    display: none;
}

.MainBackground img
{
    height: 100vh;
    width: 100vw;
    position: sticky;
}
.Home::-webkit-scrollbar
{
    display: none;
}
.navbar
{
    display: flex;
    justify-content: space-between;
    z-index: 10;
    background-color: transparent;
    width: 100%;
}
.navbar-nav
{
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.offcanvas
{
    position: fixed;
}
.nav-item{
    padding-left: 40px;
    margin: 5px;
}
.nav-link
{
    font-family: Arial, Helvetica, sans-serif;
    border-bottom: 5px solid transparent;
    border-top: 5px solid transparent;
    border-radius: 10px;
    padding: 0px;
    color: #66FCF1;
}
.active-nav-border
{
    border-bottom: 5px solid #66FCF1;
    border-top: 5px solid #66FCF1;
    border-radius: 10px;
    color: #66FCF1;
}
.nav-link:hover
{
    border-bottom: 5px solid #66FCF1;
    border-top: 5px solid #66FCF1;
}
.regis-N-Login
{
    padding: 10px;
    margin: 5px;
    margin-right: 30px;
}
.navbar-brand img
{
    width: 80px;
    border-radius: 50%;
    transition: all 0.3s linear;
    border: 2px solid white;
}
.navbar-brand img:hover
{
    transform: rotate(360deg);
}
.dropdown-menu
{
    background-color: #1F2833;
    animation: dropdown-menu 0.3s linear;
    color: white;
}
@keyframes dropdown-menu {
    0%{transform: translateY(30%);}
    100%{transform: translateY(0);}
}
.dropdown-item
{
    color: white;
    animation: dropdown-item 0.5s linear;
}
@keyframes dropdown-item {
    0%{transform: translateX(30%);}
    100%{transform: translateY(0);}
}
.your-profile
{
    background-color: transparent;
    transition: all 0.3s linear;
}
.your-profile:hover
{
    transform: scale(1.1);
}
.your-profile img
{
    width: 60px;
    border-radius: 50%;
}
#Home
{
    flex-wrap: wrap;
}
.Home-headingBox
{
    /* border: 2px solid gray; */
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: fit-content;
    border-radius: 10px;
    margin-left: 2%;
    margin-top: 2%;
    transition: all 0.5s ease;
    box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.5);
    -webkit-box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.5);
    -moz-box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.5);
    animation: pop 0.3s ease;
    background-color: #1F2833;
}
.Home-headingBox h1
{
    font-size: 4em;
}
.Home-headingBox h1,h2,h3,p
{
    color: white;
    margin-left: 10px;
    padding: 20px;
}
.Home-body-Up
{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    /* border: 2px solid black; */
}
.latest-Updates
{
    display: flex;
    flex-wrap: wrap;
    width: 700px;
    border-radius: 10px;    
    margin-top: 2%;
    margin-right: 2%;
    transition: all 0.5s ease;
    box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.5);
    -webkit-box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.5);
    -moz-box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.5);
    animation: pop 0.3s ease;
    transition: all 0.4s ease;
    background-color: #1F2833;
    padding: 10px;
}
.latest-Updates:hover
{
    transform: scale(1.06);
}
.latest-Updates h1
{
    color: #66FCF1;
    padding-left: 30%;
}
.Home-headingBox:hover
{
    transform: scale(1.06);
}
.Home-Body-Slider
{
    margin-top: 50px;
}
.custom-carousel {
    width: 89%; /* Adjust the width as needed */
    height: 600px; /* Adjust the height as needed */
    overflow:hidden; /* Hide any overflow content */
    margin-left: 5%;
    border-radius: 10px;
  }
.custom-carousel img {
    width: 100%;
    height: auto;
  }
.Home-Slider
{
    height: 100px;
    width: 100px;
}
.HomeCardSection
{
    /* border: 2px solid gray; */
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    border-radius: 10px;
    margin-left: 2%;
    margin-top: 2%;
    margin-right: 2%;
    transition: all 0.5s ease;
    box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.5);
    -webkit-box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.5);
    -moz-box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.5);
    background-color: #1F2833;
}
.Home-homeHeadings h1,h2,h3
{
    color: #66FCF1;
}
.Home-downdescription p
{
    color: #66FCF1;
}
 .gradient-colors
{
    color: #66FCF1;
    text-align: center;
}
/*
@keyframes textAnimation{
    0%{background-position: 0%;}
    100%{background-position: 400%;}
} */
.carousel-control-prev-icon,
.carousel-control-next-icon {
      background-color: blue; 
      border-radius: 50%;
}
.ProjectCard
{
    margin: 10px;
    cursor: pointer;
    border: 1px solid grey;
    border-radius: 10px;
    background-color: #222629;
    width: fit-content;
    box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.3);
    -webkit-box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.3);
    -moz-box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.3);
    transition: all 1s linear;
}
.ProjectCard img
{
    width: 300px;
}
.ProjectCard:hover
{
    transform: scale(1.06);
}
.carousel-caption {
    position: absolute;
    top: 65%; /* Adjust this value to move the caption vertically */
    left: 50%; /* Adjust this value to move the caption horizontally */
    transform: translate(-50%, -50%); /* Center the caption */
    text-align: center;
}
.carousal-text
{
    backdrop-filter: blur(10px);
    color: blue;
    font-weight: bold;
    background: rgba(0, 0, 0, 0.4);
}
.project-card-hidden
{
    opacity: 0;
    filter: blur(5px);
    transform: translateX(-100%);
}
.project-card-show
{
    opacity: 1;
    filter: blur(0);
    transform: translateX(0);
}
.about-us-desc {
    display: flex;
    flex-direction:column;
    height: fit-content;
    flex-wrap: wrap;
    border-radius: 10px;
    margin: 2%;
    box-shadow: 4px -1px 72px 0px rgba(0, 0, 0, 0.5);
    -webkit-box-shadow: 4px -1px 72px 0px rgba(0, 0, 0, 0.5);
    -moz-box-shadow: 4px -1px 72px 0px rgba(0, 0, 0, 0.5);
    padding: 10px;
    justify-content: center;
    background-color: #1F2833;
}
.about-us-desc::-webkit-scrollbar
{
    display: none;
}
.about-desc-text
{
    /* border: 2px solid white; */
    color: white;
    height: fit-content;
    padding: 0%;
    margin: 0%;
}
.about-us-goal
{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    border-radius: 10px;
    margin-left: 2%;
    margin-top: 2%;
    margin-right: 2%;
    box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.5);
    -webkit-box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.5);
    -moz-box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.5);
    color: white;
    background-color: #1F2833;
    font-size: larger;
}
.about-us-goal img
{
    padding: 50px;
    border-radius: 80px;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
.contactUsDiv
{
    border: 2px solid #66FCF1;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */
}
.contactUsDiv button
{
    padding: 10px;
    margin: 5px;
}
.contactUsDivPart {
    position: relative;
    /* margin-bottom: 2rem; */
    padding: 10px;
}
.contactUsDivPart input {
    width: 100%;
    padding: 10px 0;
    font-size: 1rem;
    background-color: transparent;
    border: none;
    border-bottom: 2px solid #ccc;
    outline: none;
    color: white;
}

.contactUsDivPart label {
    position: absolute;
    top: 20px;
    left: 10px;
    font-size: 1rem;
    color: #aaa;
    pointer-events: none;
    transition: all 0.2s ease-out;
}

.contactUsDivPart input:focus + label,
.contactUsDivPart input:not(:placeholder-shown) + label {
    top: -1px;
    left: 10px;
    font-size: 0.85rem;
    color: #66FCF1;
}

.contactUsDivPart textarea
{
    width: 100%;
    height: 100px;
    background-color: transparent;
    border: 1px solid #66FCF1;
    border-radius: 10px;
    color: white;
}
.contactUsDiv h1
{
    text-align: center;
}
.down-to-up-hidden
{
    opacity: 0;
    filter: blur(5px);
    transform: translateY(100%);
    transition: all 0,5s linear;
}
.down-to-up-show
{   
    opacity: 1;
    filter: blur(0);
    transform: translateY(0);
    transition: all 0.5s linear;
}
.left-to-right-hidden
{
    opacity: 0;
    filter: blur(5px);
    transform: translateX(-20%);
}
.left-to-right-show
{
    opacity: 1;
    filter: blur(0);
    transform: translateX(0);
    transition: all 1s linear;
}
.boxShadow
{
    box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.1);
    -webkit-box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.1);
    -moz-box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.1);
}
.Courses .cardArea
{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    border-radius: 10px;
    margin-left: 2%;
    margin-top: 2%;
    margin-right: 2%;
    box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.5);
    -webkit-box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.5);
    -moz-box-shadow: 4px -1px 72px 0px rgba(0,0,0,0.5);
    position: relative;
}
.service-desc
{
    /* border: 1px solid white; */
    border-radius: 10px;
    text-align: center;
}

.service-desc-text
{
    color: white;
    /* border: 1px solid white; */
    font-size: large;
}
.image-and-solutions
{
    display: flex;
    flex-wrap: wrap;
    margin-top: 3%;
}
.solution-image
{
    width: 50%;
}
.image-and-solutions img
{
    width: 100%;
}
.solution-text 
{   
    text-align: center;
    font-size: large;
    width: 50%;
    margin-top: 3%;
}
.service-sub
{
    /* border: 2px solid white; */
    width: 100%;
    margin-top: 5%;
    display: flex;
    flex-wrap: wrap;
}
.service-sub-text
{
    width: 50%; /* Default to 100% width */
    font-size: x-large;
    color: #66FCF1;
}
.service-sub-image 
{
    width: 50%; /* Default to 100% width */
}
.service-sub-image img
{
    width: 100%;
    border-radius: 10px;
}
.courseCard
{
    border: 2px solid gray;
    width: 330px;
    border-radius: 10px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    margin: 10px;
    padding: 10px;
    background-color: #222629;
}
.courseCard-animation-hidden
{
    opacity: 0;
    transform: translateY(100%);
    filter: blur(5px);
}
.courseCard-animation-show
{
    opacity: 1;
    transform: translateY(0);
    filter: blur(0);
    transition: all 0.5s linear;
}
.OurTeamBox
{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    background-color: #1F2833;
    border-radius: 10px;
    margin-left: 1%;
    margin-right: 1%;
    position: relative;
}
.OurTeamBoxContent
{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.OurTeamBoxImage
{
    width: 50%;
    margin-bottom: 5%;
    margin-left: 2%;
}
.OurTeamBox img
{
    width: 100%;
    border-radius: 20px;
}
.OurTeamBoxText
{
    width: 50%;
    height: fit-content;
    border-radius: 10px;
    margin-right: 2%;
    margin-left: 2%;
    padding: 10px;
}
.footerBox
{
    margin-top: 2%;
    height: fit-content;
    padding: 0px;
    width: 100%;
    position: absolute;
}

@media (min-width:350px)
{
    .Home-body-Up
    {
        font-size: 10px;
        margin-right: 2%;
    }
    .Home-headingBox:hover
    {
        transform: scale(1);
    }
    .latest-Updates:hover
    {
        transform: scale(1);
    }
    .latest-Updates
    {
        margin-left: 2%;
        margin-right: 0%;
    }
    .Home-homeHeadings h3
    {
        font-size: large;
    }
    .courseCard
    {
        padding: 1%;
        margin: 0px 0px 20px 0px;
    }
    .ProjectCard
    {
        margin: 0px 0px 15px 30px;
    }
    .latest-Updates-desc span
    {
        font-size:20px;
    }
    .Home-Body-Slider
    {
        display: none;
    }
    .about-us-desc
    {
        overflow: scroll;
    }
    .image-and-solutions
    {
        flex-direction: column; 
        justify-content: center;
        align-items: center;
    }
    .service-sub
    {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
    }
    .service-sub h1
    {
        text-align: center;
    }
    .service-sub-text
    {
        width: 100%;
    }
    .OurTeamBoxContent
    {
        flex-direction: column;
    }
    .OurTeamBoxImage
    {
        width: 100%;
    }
    .OurTeamBoxText
    {
        width: 100%;
    }
    .solution-text
    {
        width: 100%;
    }
    .loginBox
    {
        width: 340px;
    }
}
@media (min-width: 576px) 
{ 
